import React, { Component } from 'react';
import '../css/keyboard.css'

const alphabets = {
    'h':['ሀ','ሁ','ሂ','ሃ','ሄ','ህ','ሆ','ኋ'],
    'l':['ለ','ሉ','ሊ','ላ','ሌ','ል','ሎ','ሏ'],
    'h2':['ሐ','ሑ','ሒ','ሓ','ሔ','ሕ','ሖ','ሗ'],
    'm':['መ','ሙ','ሚ','ማ','ሜ','ም','ሞ','ሟ'],
    's2':['ሠ','ሡ','ሢ','ሣ','ሤ','ሥ','ሦ','ሧ'],
    'r':['ረ','ሩ','ሪ','ራ','ሬ','ር','ሮ','ሯ'],
    's':['ሰ','ሱ','ሲ','ሳ','ሴ','ስ','ሶ','ሷ'],
    'sh':['ሸ','ሹ','ሺ','ሻ','ሼ','ሽ','ሾ','ሿ'],
    'q':['ቀ','ቁ','ቂ','ቃ','ቄ','ቅ','ቆ','ቋ'],
    'b':['በ','ቡ','ቢ','ባ','ቤ','ብ','ቦ','ቧ'],
    'v':['ቨ','ቩ','ቪ','ቫ','ቬ','ቭ','ቮ','ቯ'],
    't':['ተ','ቱ','ቲ','ታ','ቴ','ት','ቶ','ቷ'],
    'ch':['ቸ','ቹ','ቺ','ቻ','ቼ','ች','ቾ','ቿ'],
    'n':['ነ','ኑ','ኒ','ና','ኔ','ን','ኖ','ኗ'],
    'gn':['ኘ','ኙ','ኚ','ኛ','ኜ','ኝ','ኞ','ኟ'],
    'e':['አ','ኡ','ኢ','ኣ','ኤ','እ','ኦ','ኧ'],
    'a':['አ','ኡ','ኢ','ኣ','ኤ','እ','ኦ','ኧ'],
    'k':['ከ','ኩ','ኪ','ካ','ኬ','ክ','ኮ','ኳ'],
    'w':['ወ','ዉ','ዊ','ዋ','ዌ','ው','ዎ',''],
    'z':['ዘ','ዙ','ዚ','ዛ','ዜ','ዝ','ዞ','ዟ'],
    'zh':['ዠ','ዡ','ዢ','ዣ','ዤ','ዥ','ዦ','ዧ'],
    'y':['የ','ዩ','ዪ','ያ','ዬ','ይ','ዮ','ዯ'],
    'd':['ደ','ዱ','ዲ','ዳ','ዴ','ድ','ዶ','ዷ'],
    'j':['ጀ','ጁ','ጂ','ጃ','ጄ','ጅ','ጆ','ጇ'],
    'g':['ገ','ጉ','ጊ','ጋ','ጌ','ግ','ጎ','ጏ'],
    'x':['ጠ','ጡ','ጢ','ጣ','ጤ','ጥ','ጦ','ጧ'],
    'c':['ጨ','ጩ','ጪ','ጫ','ጬ','ጭ','ጮ','ጯ'],
    'ts2':['ጰ','ጱ','ጲ','ጳ','ጴ','ጵ','ጶ','ጷ'],
    'ph':['ጰ','ጹ','ጺ','ጻ','ጼ','ጽ','ጾ','ጷ'],
    'ts':['ፀ','ፁ','ፂ','ፃ','ፄ','ፅ','ፆ','ፇ'],
    'f':['ፈ','ፉ','ፊ','ፋ','ፌ','ፍ','ፎ','ፏ'],
    'p':['ፐ','ፑ','ፒ','ፓ','ፔ','ፕ','ፖ','ፗ'],
    ' ':[' ',' ',' ',' ',' ',' ',' ',' '],
    '-':['፡'],
    ',':['፣'],
    '.':['።'],
    ';':['፤'],
    ':':['፥']
    }

export default class Translate extends Component {
    
    constructor(props){
        super(props)
        this.state = {
            tranformed: "hello",
            text_value: ""
        }
    }
    handleChangeInput = (e,s) => {
        
        this.setState({[s]: e.target.value}, ()=>{
            this.transform2(this.state.text_value)
            console.log(this.state)
    })
    }
    

    isVowel(char){
        const vowels = ['a','e','i','o','u']
        if(vowels.includes(char)) return true 
        return false
    }   

    doesNeedH(char){
        const letters = ['s','c','z','p']
        if(letters.includes(char)) return true 
        return false
    }

    isCh(str){
        const lts = ['ch', 'sh','zh','ph','gn','ts']
        if(lts.includes(str)) return true 
        return false
    }
    isPunctuation(char){
        const allowed_puncs = ['.',',','-',':',';']
        if(allowed_puncs.includes(char)) return true 
        return false
    }

    getLetter(a,b){
        if(a in alphabets){
            switch(b){
                case 'a': return alphabets[a][3]
                case 'e': return alphabets[a][0]
                case 'i': return alphabets[a][2]
                case 'o': return alphabets[a][6]
                case 'u': return alphabets[a][1]
                default: return ""
            }
        }
        
        return ""
    }

   processSubstring(str){
        const ln = str.length
        if(ln == 0){
            return ""
        }
        if(ln == 1){
            console.log(": ", str)
            if(str in alphabets){
                if(this.isPunctuation(str) || str == 'a'){
                    return alphabets[str][0]
                }

                return alphabets[str][5]
            }
            return str
        }
        else if(ln == 2){
            console.log("2: ", str)
            if(str[0] == ' ' && str[1] == 'e'){
                return ' ' + alphabets['e'][5]
            }
            if(this.isCh(str)){
                return alphabets[str][5]
            }
            if(this.isVowel(str[0]) && this.isVowel(str[1])){
                if(str[0] == 'e'){
                    return this.getLetter(str[0],str[1])
                }

                if(str[0] != 'e' && str[1] != 'e' || str[0] != 'a' && str[1] != 'a'){
                    return ""
                }
            }
            if(str[1] == 'i'){
                return alphabets[str[0]][5]
            }
            if(str[0] in alphabets && !this.isPunctuation(str[0])){
                if(str[0] == ' ' && str[1] == 'a'){
                    return ' ' + alphabets['a'][0]
                }
                return this.getLetter(str[0],str[1])
            }

            return str
        }
        else if( ln == 3){
            const f2 = str.slice(0,2)
            const l2 = str.slice(-2)

            if(str[0] == ' ' && l2 == "ee"){
                return ' ' + alphabets['e'][0]
            }

            if(this.isCh(f2)){
                if(str[2] == 'i'){
                    return alphabets[f2][5]
                }
                return this.getLetter(f2,str[2])
            }

            if(str == "eee"){
                return alphabets[str[0]][4]
            }

            if(l2 == "ee"){
                return alphabets[str[0]][4]
            }
            if(l2 == "ua"){
                return alphabets[str[0]][7]
            }
            if(l2 == 'ii'){
                if(str[0] != 'i'){
                    return alphabets[str[0]][2]
                }

                return ""
            }
        }
        else if(ln == 4){
            const f2 = str.slice(0,2)
            const l2 = str.slice(-2)
            const l3 = str.slice(-3)

            if(l3 == "eee" && str[0] == ' '){
                return alphabets['e'][4]
            }

            if(l2 == "ee"){
                if(this.isCh(f2)){
                    return alphabets[f2][4]
                }
                return alphabets[str[0]][4]
            }
            if(l2 == "ua"){
                return alphabets[f2][7]
            }
            if(l2 == "ii"){
                    if(this.isCh(f2)){
                        return alphabets[f2][2]
                    }
                    return alphabets[str[0]][2]
            }
        }
        else{
            return this.processSubstring(str.slice(0,4))
        }
   }

   transform(text){
        var text = text.trim()
        
        const lenx = text.length
        var substr = ""
        var transformed = ""
        var i = 0
        
        while(i < lenx){
            if(substr == ""){
                substr += text[i]
            }
            else{
                if(this.doesNeedH(substr.slice(-1)) && text[i] == 'h'){
                    substr += text[i]
                }
                else if (substr.slice(-1) == 't' && text[i] == 's'){
                    substr += text[i]
                }
                else if(substr.slice(-1) == 'g' && text[i] == 'n'){
                    substr += text[i]
                }

                else if(this.isVowel(text[i])){
                    if(this.isVowel(substr.slice(-1)) && this.isVowel(text[i])){
                        if(text[i] == 'a' && substr.slice(-1) != 'u'){
                            transformed += this.processSubstring(substr)
                            substr = text[i]
                        }

                        else if((substr.slice(-1) == 'e' && text[i] == 'e') || (substr.slice(-1) == 'u' && text[i]) == 'a' || (substr.slice(-1) == 'i' && text[i] == 'i')){
                            substr += text[i]
                        }

                        else if(lenx == 2 && substr[0] == 'e' && this.isVowel(text[i])){
                            substr += text[i]
                        }
                    }
                    else{
                        substr += text[i]
                    }
                }
                else{
                    transformed += this.processSubstring(substr) 
                    substr = text[i]
                }
            }
                    
            i += 1
        }
        
        transformed += this.processSubstring(substr)
        return transformed
    }
    transform2(str){
        const txt = str.toLowerCase().split("\n")
        var transformed = ""

        for(var i = 0;i < txt.length;i++){
            transformed += this.transform(txt[i]) + "\n"
        }
        this.setState({transformed:transformed})
    }

    render() {
        return (
            <div className=" main-wrapper">
            <header className="logo">
                Amharic.io
            </header>
              <div className="card transform-wrapper shadow-sm">
                <div className="card-header">
                    Amharic keyboard
                </div>
                <div className="card-body type-wrapper">
                   <div className="row">
                    <div className="col">
                        <textarea 
                            placeholder="Type here..."
                            onChange={(e) => {this.handleChangeInput(e,"text_value")}}
                        >
                        </textarea>
                    </div>
                    <div className="col transformed_text">{this.state.transformed}</div>
                   </div>
                </div>
                </div>
               <div className=" row how-to">
               <div className="col">
               <h6>HOW TO TYPE</h6>
               <span>
                   The keyboard works by transforming the combination of vowels and consonants in Latin
                   in to the equaivalent of Amharic letters. <br/><br/>
                   
                   There are 7-8 variations of each letter. Take the Amharic letter <b>ተ</b> as an example and its varitations are below along with the
                   Latin letters used to write them. Follow the same pattern for other letters.
                   <br/><br/>
                   <table className="table table-bordered table-stripped">
                       <thead>
                           <tr>
                               <th>ተ</th>
                               <th>ቱ</th>
                               <th>ቲ</th>
                               <th>ታ</th>
                               <th>ቴ</th>
                               <th>ት</th>
                               <th>ቶ</th>
                               <th>ቷ</th>
                           </tr>
                       </thead>
                       <tbody>
                           <tr>
                               <td>te</td>
                               <td>tu</td>
                               <td>tii</td>
                               <td>ta</td>
                               <td>tee</td>
                               <td>t</td>
                               <td>to</td>
                               <td>tua</td>
                           </tr>
                       </tbody>
                   </table>

                   <br/>
                   Other Letters<br/>
                   <table className="table table-bordered table-stripped">
                       <thead>
                           <tr>
                               <th>ሸ</th>
                               <th>ፀ</th>
                               <th>ጨ</th>
                               <th>ቸ</th>
                               <th>ኘ</th>
                               <th>ዠ</th>
                               <th>ጠ</th>
                               <th>ጰ</th>
                               <th>ቀ</th>
                               
                           </tr>
                       </thead>
                       <tbody>
                           <tr>
                               <td>she</td>
                               <td>tse</td>
                               <td>ce</td>
                               <td>che</td>
                               <td>gne</td>
                               <td>zhe</td>
                               <td>xe</td>
                               <td>phe</td>
                               <td>qe</td>
                               
                           </tr>
                       </tbody>
                   </table>
               </span>
               </div>
               <div className="col">
               <h6>EXAMPLES</h6>
              
               <table className="table table-bordered table-stripped">
                       <thead>
                           <tr>
                               <th>Latin</th>
                               <th>Amharic</th>
                              
                           </tr>
                       </thead>
                       <tbody>
                           <tr>
                               <td>mexahugn zinahn semichee.</td>
                               <td>መጣሁኝ ዝናህን ሰምቼ።</td>
                           </tr>
                           <tr>
                               <td>wanawu lav new man!</td>
                               <td>ዋናዉ ላቭ ነው ማን!</td>
                           </tr>
                           <tr>
                                <td>endeet newu selam nachihu?</td>
                               <td>እንዴት ነዉ ሰላም ናችሁ?</td>
                           </tr>
                           <tr>
                                <td>min xiqim.</td>
                               <td>ምን ጥቅም።</td>
                           </tr>
                       </tbody>
                   </table>
               </div>
               </div>
               <div className="footer">
               &copy; 2019. App by <a href="https://github.com/magna25">Henok Hailemariam</a> Checkout the <a href="https://github.com/magna25/amharic-keyboard">Source</a>
               </div>
            </div>
        )
    }
}